import { useMemo } from 'react'
import shuffle from '../utils/shuffle'

/**
 * useOptions ‚Äî –ø—Ä–∏–Ω–∏–º–∞–µ—Ç:
 *   - allWords: –ø–æ–ª–Ω—ã–π –º–∞—Å—Å–∏–≤ —Å–ª–æ–≤ (–≤ —Ç–æ–º —á–∏—Å–ª–µ –Ω–µ —Ç–æ–ª—å–∫–æ —Ç—Ä–µ–Ω–∏—Ä–æ–≤–æ—á–Ω—ã—Ö)
 *   - words:   –º–∞—Å—Å–∏–≤ —Ç–µ–∫—É—â–∏—Ö ¬´—Ç—Ä–µ–Ω–∏—Ä–æ–≤–æ—á–Ω—ã—Ö¬ª —Å–ª–æ–≤ (–æ—Ç–∫—É–¥–∞ –±–µ—Ä—ë—Ç—Å—è —Å–ª–æ–≤–æ –ø–æ safeIndex)
 *   - safeIndex: –∏–Ω–¥–µ–∫—Å —Ç–µ–∫—É—â–µ–≥–æ —Å–ª–æ–≤–∞ –≤ –º–∞—Å—Å–∏–≤–µ words
 *   - direction: 'en-ru' –∏–ª–∏ 'ru-en'
 *
 * –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç:
 *   - options: –º–∞—Å—Å–∏–≤ –∏–∑ 4 –≤–∞—Ä–∏–∞–Ω—Ç–æ–≤ (–ø—Ä–∞–≤–∏–ª—å–Ω—ã–π + 3 ¬´–æ—Ç–≤–ª–µ–∫–∞—é—â–∏—Ö¬ª), —É–∂–µ —Å–ª—É—á–∞–π–Ω–æ –ø–µ—Ä–µ–º–µ—à–∞–Ω–Ω—ã–π
 *   - correctAnswer: –ø—Ä–∞–≤–∏–ª—å–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ (–Ω–∏–∑–∫–∏–π —Ä–µ–≥–∏—Å—Ç—Ä, –¥–ª—è –ø—Ä–æ—Å—Ç–æ–≥–æ —Å—Ä–∞–≤–Ω–µ–Ω–∏—è)
 */

export function useOptions(allWords, words, safeIndex, direction) {
  return useMemo(() => {
    if (
      !Array.isArray(words) ||
      words.length === 0 ||
      safeIndex < 0 ||
      safeIndex >= words.length
    ) {
      return { options: [], correctAnswer: null }
    }

    const word = words[safeIndex]
    if (!word || !Array.isArray(allWords) || allWords.length === 0) {
      return { options: [], correctAnswer: null }
    }

    const pool = allWords
      .filter(w => w.id !== word.id)
      .map(w => (direction === 'en-ru' ? w.russian : w.english))

    const distractors = shuffle(pool).slice(0, 3)

    const correctVal = direction === 'en-ru' ? word.russian : word.english

    const options = shuffle([correctVal, ...distractors])

    return {
      options,
      correctAnswer: correctVal.toLowerCase(),
    }
  }, [allWords, words, safeIndex, direction])
}