import { useState, useEffect } from 'react'

/**
 * useWordNavigation ‚Äî —Ö—É–∫, –∫–æ—Ç–æ—Ä—ã–π —Ö—Ä–∞–Ω–∏—Ç —Ç–µ–∫—É—â–∏–π –∏–Ω–¥–µ–∫—Å, 
 * –≤—ã—á–∏—Å–ª—è–µ—Ç –±–µ–∑–æ–ø–∞—Å–Ω—ã–π –∏–Ω–¥–µ–∫—Å –∏ —Ç–µ–∫—É—â–µ–µ —Å–ª–æ–≤–æ, –∞ —Ç–∞–∫–∂–µ –¥–∞—ë—Ç —Ñ—É–Ω–∫—Ü–∏–∏
 * –¥–ª—è –ø–µ—Ä–µ—Ö–æ–¥–∞ –∫ —Å–ª–µ–¥—É—é—â–µ–º—É –∏ —Å–±—Ä–æ—Å–∞ –Ω–∞–≤–∏–≥–∞—Ü–∏–∏.
 *
 * @param {Array} words ‚Äî –º–∞—Å—Å–∏–≤ —Å–ª–æ–≤ (–∫–∞–∂–¥—ã–π —ç–ª–µ–º–µ–Ω—Ç {id, english, russian, ‚Ä¶})
 * @returns {{
 *   safeIndex: number,   // –∏–Ω–¥–µ–∫—Å, –Ω–µ –≤—ã—Ö–æ–¥—è—â–∏–π –∑–∞ –≥—Ä–∞–Ω–∏—Ü—ã –º–∞—Å—Å–∏–≤–∞
 *   word: object|null,   // —Ç–µ–∫—É—â–µ–µ —Å–ª–æ–≤–æ –∏–ª–∏ null
 *   currentId: number|null, // id —Ç–µ–∫—É—â–µ–≥–æ —Å–ª–æ–≤–∞
 *   next: () => void,    // —Ñ—É–Ω–∫—Ü–∏—è ¬´—Å–ª–µ–¥—É—é—â–µ–µ —Å–ª–æ–≤–æ¬ª
 *   reset: () => void    // —Ñ—É–Ω–∫—Ü–∏—è ¬´–Ω–∞—á–∞—Ç—å —Å–Ω–∞—á–∞–ª–∞¬ª
 * }}
 */

export function useWordNavigation(words) {
  const [index, setIndex] = useState(0)

  const empty = !Array.isArray(words) || words.length === 0
  const safeIndex = empty ? 0 : Math.min(index, words.length - 1)
  const word = empty ? null : words[safeIndex]
  const currentId = word?.id ?? null

  useEffect(() => {
    setIndex(0)
  }, [words])

  const next = () => {
    if (empty) return
    setIndex(prev => {
      return prev < words.length - 1 ? prev + 1 : prev
    })
  }

  const reset = () => {
    setIndex(0)
  }

  return { safeIndex, word, currentId, next, reset, empty }
}
