import { useState, useEffect } from 'react'

const STORAGE_KEY = 'vocabProgress'
const DEFAULT = { correctStreak: 0, knownByUser: false }

/**
 * –•—É–∫ —Ö—Ä–∞–Ω–∏—Ç –¥–ª—è –∫–∞–∂–¥–æ–≥–æ wordId:
 *  - correctStreak: —Å–∫–æ–ª—å–∫–æ —Ä–∞–∑ –ø–æ–¥—Ä—è–¥ —É–≥–∞–¥–∞–Ω–æ –≤ —Ç–µ—Å—Ç–µ
 *  - knownByUser
 *
 * –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç [state, saveProgress, resetProgress]
 */
export function useProgress(wordId) {
  const [state, setState] = useState(DEFAULT)

  useEffect(() => {
    const all = JSON.parse(localStorage.getItem(STORAGE_KEY) || '{}')
    setState(all[wordId] || DEFAULT)
  }, [wordId])

  const saveProgress = updates => {
    setState(prev => {
      const next = { ...prev, ...updates }
      const all  = JSON.parse(localStorage.getItem(STORAGE_KEY) || '{}')
      all[wordId] = next
      localStorage.setItem(STORAGE_KEY, JSON.stringify(all))
      return next
    })
  }

  const resetProgress = () => {
    setState(DEFAULT)
    const all = JSON.parse(localStorage.getItem(STORAGE_KEY) || '{}')
    delete all[wordId]
    localStorage.setItem(STORAGE_KEY, JSON.stringify(all))
  }

  return [state, saveProgress, resetProgress]
}

/**
 * –ì–ª–æ–±–∞–ª—å–Ω–∞—è —Ñ—É–Ω–∫—Ü–∏—è –¥–ª—è —Å–±—Ä–æ—Å–∞ –ø—Ä–æ–≥—Ä–µ—Å—Å–∞ –≤–Ω–µ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞
 */
export function resetProgress(wordId) {
  const all = JSON.parse(localStorage.getItem(STORAGE_KEY) || '{}')
  delete all[wordId]
  localStorage.setItem(STORAGE_KEY, JSON.stringify(all))
}
