/**
 * –ü—Ä–æ—Å—Ç–∞—è —Ñ—É–Ω–∫—Ü–∏—è –¥–ª—è —Å–ª—É—á–∞–π–Ω–æ–≥–æ –ø–µ—Ä–µ–º–µ—à–∏–≤–∞–Ω–∏—è –º–∞—Å—Å–∏–≤–∞
 * @param {Array} arr ‚Äî –≤—Ö–æ–¥–Ω–æ–π –º–∞—Å—Å–∏–≤
 * @returns {Array} ‚Äî –Ω–æ–≤—ã–π –º–∞—Å—Å–∏–≤ —Å —Ç–µ–º–∏ –∂–µ —ç–ª–µ–º–µ–Ω—Ç–∞–º–∏, –Ω–æ –≤ —Å–ª—É—á–∞–π–Ω–æ–º –ø–æ—Ä—è–¥–∫–µ
 */

export default function shuffle(arr) {
  return arr
    .map(v => ({ sort: Math.random(), value: v }))
    .sort((a, b) => a.sort - b.sort)
    .map(o => o.value)
}
